html {
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: inherit;
}
div {
  background-color: #282c34;
}

.App {
  text-align: center;
  color: white;
  font-size: calc(11px + 2vmin);
}

.App-header {
  height: 10%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-footer {
  height: 10%;
}

.App-link {
  color: #61dafb;
}

.App-player {
  height: 200px;
  padding-top: 15%;
  padding-bottom: 15%;
}

.App-body {
  height: 80%;
}

.game-links {
  height: 40%;
}

.new-game {
  padding: 20%;
  color: gray;
}
.small {
  font-size: 14px;
}

button {
  font-size: 28px;
  border-color: gray;
  border-radius: 5px;
}

a {
  text-decoration: underline;
}

form {
  padding-bottom: 50%;
}

input {
  border-radius: 5px;
}
.Player {
  border-style: solid;
  font-size: 18px;
  border-color: darkgray;
}

.Player-current {
  border-color: white;
}

.Player-winner {
  background-color: #eab800;
  border-style: hidden;
  -webkit-animation-name: score-transition-winner;
          animation-name: score-transition-winner;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-iteration-count: 50;
          animation-iteration-count: 50;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
}

.Player-container {
  height: 10%;
  justify-content: center;
}

.Player-score {
  height: 5px;
  background-color: yellow;
}

@-webkit-keyframes score-transition-winner {
  0%   {
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
  }
  25%  { -webkit-transform: scale(1.05,1.25); transform: scale(1.05,1.25); }
  50%  { -webkit-transform: scale(0.95, 0.95); transform: scale(0.95, 0.95); }
  75%  { -webkit-transform: scale(1.1,1.30); transform: scale(1.1,1.30); }
  100% {
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
  }
}

@keyframes score-transition-winner {
  0%   {
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
  }
  25%  { -webkit-transform: scale(1.05,1.25); transform: scale(1.05,1.25); }
  50%  { -webkit-transform: scale(0.95, 0.95); transform: scale(0.95, 0.95); }
  75%  { -webkit-transform: scale(1.1,1.30); transform: scale(1.1,1.30); }
  100% {
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
  }
}
.Prompt {
  font-size: 24px;
}

input {
  font-size: 28px;
}
.Vote {
  font-size: 28px;
}

.Vote-option {
  font-size: 28px;
}
